import React, { Component } from 'react';
import { Modal, Button ,ButtonGroup, Container, Row, Col,Navbar } from 'react-bootstrap';
import {FaCheck} from 'react-icons/fa';

import listeCommandes from './commandes';

class EntrezNum extends Component{
    constructor(){
        super()
        this.state={
            value:'',
        }
    }
    handleChange(event) {
        this.setState({value: event.target.value});
      }

    handleSubmit = (e)=> {
        e.preventDefault()
        listeCommandes.numTable = parseInt(this.state.value);
        alert('Le nom a été soumis : ' + listeCommandes.numTable);
        this.props.handleNum()
    }
    render() {
    
        return (
            <div>
                <Container>
                    <Modal
                        /*{...props}*/
                        size="md"
                        aria-labelledby="contained-modal-title-vcenter"
                        centered
                        dialogClassName="modal-80w"
                        show={this.props.show}
                        onHide = {this.props.onHide}
                        >
                        <Modal.Header closeButton>
                          
                        </Modal.Header>

                        <Modal.Body>
                        <form onSubmit = {e => this.handleSubmit(e)}>
                            <Container >
                                <Row >
                                
                                <Col>
                                    <input 
                                        className="form-control"
                                        placeholder ='Entrez le numero de la table' 
                                        value={this.state.value}
                                        onChange={e => this.handleChange(e)}/>
                                </Col>
                                <Col>
                                    <Button type = 'submit' >
                                     < FaCheck />
                                    </Button> 
                                </Col>
                                                                    
                                </Row>
                            </Container>
                            </form>
                        </Modal.Body>
                    </Modal>
                </Container>
            </div>
        );
    }
}

export default EntrezNum;