import React, { Component } from 'react';
import shuffle from 'lodash.shuffle';
import { Modal, Button ,ButtonGroup, Container, Row, Col,Navbar } from 'react-bootstrap';
import DataCommande from './components/data_commandes'
import {FaShoppingBasket} from 'react-icons/fa';
import { BrowserRouter,Switch, Route, NavLink} from 'react-router-dom';
import * as firebase from 'firebase';
import "firebase/database";

import listeCommandes from './components/commandes';

import Cards from './components/cards';
import Commandes from './components/commandesClient';
import EntrezNum from './components/entrezNum';
import './App.css';

import { categories,boissons, menu, plats, desserts } from './components/app_data';
import config from './components/config';



class App extends Component {
  constructor(){
    super()
    this.state = {
      len: listeCommandes.content.length,
      loading: true,
      numShow: true,
    }
    
    if (!firebase.apps.length) {
      firebase.initializeApp(config);
   }
   

  }
  handleNum = () =>{ this.setState({numShow: !this.state.numShow}) }

  componentWillMount(){
    const db = firebase.database().ref("/commandes")
    db.on('value', (snapshot) => {
        this.setState({
          commandes: Object.values(snapshot.val()),
          loading: false,
        })
    })
  }
  
  
  handleClick = (inc) => this.setState( {len: this.state.len+inc})
  render() {
    if(this.state.loading){
      return (
        <h1>Chargement....</h1>
      )
    }
    
    return (
      
      <BrowserRouter>
        <div >
        
          <Navbar expand="lg" variant="dark" bg="dark" >
            <Container>
    <NavLink to = "/">Resto</NavLink>
    <NavLink to = "/commande" className = "text-white"><span className="badge badge-pill badge-warning">{this.state.len}</span><FaShoppingBasket /></NavLink>
            </Container>
            
          </Navbar>
          <div className = "d-flex justify-content-center pt-2 pb-2 bg-warning">
              <ButtonGroup  aria-label="Basic example" size = "md" color="warning">
                <Button  variant="dark"><NavLink to ="/plats" color="warning" color = 'with' ><span className = "navLink"> Plats</span></NavLink></Button>
                <Button variant="dark"><NavLink to ="/desserts" color="warning" color = 'with' ><span className = "navLink"> Desserts</span></NavLink></Button>
                <Button variant="dark"  ><NavLink to ="/boisson" color="warning" color = 'with' ><span className = "navLink"> Boisson</span></NavLink></Button>
              </ButtonGroup>
            </div>
        </div>
        <Switch>
          <Route 
            path = "/admin"
            render= {(props) => <DataCommande {...props} entries = {this.state.commandes} />} />
          
          <Route
            path = "/boisson"
            render= {(props) => <Cards {...props}  entries = {boissons} handleClick = {this.handleClick} />} />

          <Route
            path = "/desserts"
            render= {(props) => <Cards {...props}  entries = {desserts} handleClick = {this.handleClick} />} />

          <Route
            path = "/plats"
            render= {(props) => <Cards {...props}  entries = {plats} handleClick = {this.handleClick} />} />

          <Route
            path = "/commande" 
            render = {(props) => <Commandes {...props} handleClick = {this.handleClick}/>} />

          <Route 
            path = "/numtable"
            render = {(props) => <EntrezNum {...props} show={this.state.numShow}
            onHide={() => this.setState({numShow:false})} handleNum = {this.handleNum}/>}/>

          <Route path = "/" 
            render= {(props) => <Cards {...props}  entries = {categories} handleClick = {this.handleClick} />} />
          
        </Switch>
      </BrowserRouter>
    )
  }
}

export default App
