import React, { Component } from 'react';
import { Modal, Button, ButtonGroup, Container, Row, Col } from 'react-bootstrap';
import {FaEye, FaShoppingCart} from 'react-icons/fa';

import 'bootstrap/dist/css/bootstrap.min.css';


import './bootstrap.min.css'
import './card_item.css'
import MyModal from './modal'
import Add from './add'



class CardItem extends Component {
        constructor(props){
            super(props)
            this.state={
                modalShow: false,
                addShow: false,
               
            }
            
        }

        handleShow = () => {this.setState({addShow: !this.state.addShow})}
    
    render() {
        const  {id, title, src,price, desc } = this.props
        return (
            <Col>
                <div className = 'cardItem border border-secondary rounded-lg d-flex justify-content-center flex-column shadow-sm'>
                        <div className = 'title bg-secondary'>
                            <h6 className='  text-white d-flex justify-content-center font-weight-bold'>
                                {title}
                            </h6>
                        </div>
                        <div>
                            <img src = {src} className="img-fluid" />
                        </div>
                    
                        <div className = 'd-flex text-warning'>
                            <p className = 'prix'> {price + ' f'} </p>
    
                        </div>
                                    
                    <ButtonGroup aria-label="Basic example">
                        <Button variant="dark" onClick={() => this.setState({modalShow:true})}><FaEye /></Button>
                        <Button variant="success" onClick={() => this.setState({addShow:true})}><FaShoppingCart /></Button>
                    </ButtonGroup>
                    
                    <div className ='conatainer'>
                        <MyModal
                            show={this.state.modalShow}
                            onHide={() => this.setState({modalShow:false})}
                            title = {title}
                            src = {src}
                            price = {price}
                            desc = {desc}
                        />
                    </div> 
                    <Add
                        show={this.state.addShow}
                        handleShow = {this.handleShow}
                        onHide={() => this.setState({addShow:false})}
                        handleClick = {this.props.handleClick}
                        title = {title}
                        src = {src}
                        price = {price}
                        id = {id}
                    
                    />               
            </div>
            </Col>
        );
    }

}



export default CardItem