import React, { Component } from 'react';
import {Container, Row} from 'react-bootstrap';




import './bootstrap.min.css'
import './cards.css'

import CardItem from './card_item'

class Cards extends Component{
   
    render() {
        return (
          
            <div class="container-fluid cards">
              <Container fluid>
                <Row xm = {2} md ={3} xl = {6}>
                    { this.props.entries.content.map(({id, title, icon2, price,})=>(
                      <CardItem id ={id} title = {title} src = {icon2} price= {price} desc ='Sandwich: Poulet Frit, Tomate, Salade, Mayonnaise + Frites + Boisson' key = {id} handleClick = {this.props.handleClick} />
                    ))}
                </Row>
              </Container>
            </div>
            
        );
    }
}

export default Cards