import React, { Component,} from 'react';
import { Modal, Button, Container, Row, Col } from 'react-bootstrap';
import {FaCheck} from 'react-icons/fa';
import 'bootstrap/dist/css/bootstrap.min.css';
import listeCommandes from './commandes';


class Add extends Component {
    constructor(props) {
        super();
        this.state = {
          count: 0,
        };
      }

      addNewCom = (id,title,src,price,q)=>{
        console.log((listeCommandes))
        let newCom = {
                id: id,
                title: title,
                icon: src,
                quantite:q,
                price: price,
        }
        listeCommandes.content.push(newCom)
        
    }

    handleSubmit = (e)=> {
        e.preventDefault()
        this.addNewCom(this.props.id, this.props.title, this.props.src, this.props.price,this.newCom.value)
        this.props.handleClick(parseInt(1))
        this.props.handleShow()
    }
    
    render() {
        return (
            <div>
                <Container>
                    <Modal
                        /*{...props}*/
                        size="md"
                        aria-labelledby="contained-modal-title-vcenter"
                        centered
                        dialogClassName="modal-80w"
                        show={this.props.show}
                        onHide = {this.props.onHide}
                        >
                        <Modal.Header closeButton>
                          
                        </Modal.Header>

                        <Modal.Body>
                        <form onSubmit = {e => this.handleSubmit(e)}>
                            <Container >
                                <Row xs={4} md={4} >
                                
                                <Col>
                                    <Button onClick={() => this.setState({ count: this.state.count - 1 })}>
                                        -
                                    </Button>
                                </Col>
                                <Col><input className="form-control" readonly value = {this.state.count}
                                ref = { input => this.newCom = input }/></Col>
                                <Col>
                                    <Button onClick={() => this.setState({ count: this.state.count + 1 })}>
                                     +
                                    </Button> 
                                </Col>
                                <Col>
                                    <Button  type = 'submit' >
                                     < FaCheck />
                                    </Button> 
                                </Col>
                                                                    
                                </Row>
                            </Container>
                            </form>
                        </Modal.Body>
                    </Modal>
                </Container>
            </div>
        );
    }
}

export default Add;
 