import firebase from "./firebase";
import React, { Component } from 'react';
const db = firebase.ref("/commandes");

 class DataCommande extends Component {

  getAll() {
    return db;
  }

  create(comm) {
    return db.push(comm);
  }

  update(key, value) {
    return db.child(key).update(value);
  }

  delete(key) {
    return db.child(key).remove();
  }

  deleteAll() {
    return db.remove();
  }

  
   render() {
    
     return (
       <div>
         {
           this.props.entries.map(item =>(
             <ul className="list-group">
               <li className="list-group-item list-group-item-action active">{item.numTable}</li>
                 {
                   item.content.map(el =>(
                    <li className="list-group-item">
                    <div className="container">
                        <div className="row">
                            <div className='col-4'>
                                {el.title}
                            </div>
                            <div className='col-3'>
                                {el.quantite}
                            </div>
                            <div className='col-3'>
                                {el.price}
                            </div> 
                        </div>
                    </div>
                    </li>
                   ))
                 }
             </ul>
           ))
         }
         
       </div>
     );
   }
 }

export default DataCommande;