import React, { Component } from 'react';
import listeCommandes from './commandes';
import Confirmation from './confirmation';
import {FaTrash} from 'react-icons/fa';
import {Button} from 'react-bootstrap';
import * as firebase from 'firebase';
import "firebase/database";

import 'bootstrap/dist/css/bootstrap.min.css';
import config from './config';


class Commandes extends Component {
    constructor(props){
        super()
        this.state = {
            numTab : listeCommandes.numTable,
            commande: listeCommandes.content,
            toastShow: false,
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(config);
        }
    }
    

    



    handleConfirm = (data) =>{
        
        const ref = firebase.database().ref("/commandes")
        ref.push(data);
        this.setState({toastShow:true})
    }

    

    handleTrash = (id) => {
        var array = this.state.commande;
        array.splice(id,1)

        this.setState( { commande: array });
        this.props.handleClick(parseInt(-1))
    }
    render() {
        if (this.state.commande.length == 0){
            return <div className="alert alert-primary" role="alert">
                   Aucun plat selectionée
                </div>
        
        }
        return (
            <div>
                <ul class="list-group">
                    <li className="list-group-item active">
                            <div className="container">
                                <div className="row">
                                    <div className='col-4'>
                                        Nom
                                    </div>
                                    <div className='col-3'>
                                        Qte
                                    </div>
                                    <div className='col-3'>
                                        Prix
                                    </div>
                                    <div className='col-2'>
                                        Actions
                                    </div>
                                </div>
                            </div>
                    </li>
                    {this.state.commande.map((commande,index)=>(
                        
                        <li className="list-group-item">
                            <div className="container">
                                <div className="row">
                                    <div className='col-4'>
                                        {commande.title}
                                    </div>
                                    <div className='col-3'>
                                        {commande.quantite}
                                    </div>
                                    <div className='col-3'>
                                        {commande.price}
                                    </div>
                                    <div className='col-2'>
                                        <FaTrash  onClick = {()=> this.handleTrash(index)}/>
                                    </div>
                                    
                                </div>
                            </div>
                        </li>
                    ))}
                </ul>
                <Button onClick={() => this.handleConfirm(listeCommandes)}>Confirmation</Button>
                <Confirmation
                    show = {this.state.toastShow}
                    onHide={() => this.setState({toastShow:false})}
                />
            </div>
        );
    }

}

export default Commandes;